<template>
  <div class="df">
    <LeftTool @setting="getSetting" ref="left"></LeftTool>
    <div v-if="drawer&&settingItem" style="width:60%;height:100%;position:fixed" @click="open(false)"></div>
    <div class="rightTool" :class="{'openRight':drawer}" @click="open(true)">
      <el-form v-if="settingItem&&type=='form'" :model="settingItem.im.option">
        <el-form-item v-for="im in options" :label="im.title" :key="im.key">
          <component :is="'Auto' + im.type" v-model="im.option" @blur="blur"></component>
        </el-form-item>
      </el-form>
      <div v-if="settingItem&&type==='chart'">
        <!-- <el-form-item> -->
        <ChartSetting v-model="settingItem.im.option" ref='chart'></ChartSetting>
        <!-- </el-form-item> -->
      </div>
    </div>
  </div>
</template>

<script>
import LeftTool from '@/components/leftTool';
import handel from '@/components/tools/handelCom';
export default {
  name: 'autoForm',
  props: {},
  data() {
    return {
      settingItem: null,
      options: [],
      chart: ['row'],
      drawer: false,
      type: 'form',
      direction: 'rtl',
    };
  },
  components: {
    LeftTool,
  },
  methods: {
    getSetting(e) {
      if (e) {
        this.options = [];
        this.settingItem = e;
        this.type = e.im.type;
        if (this.type === 'form') {
          Object.keys(e.im.option).map((i) => {
            let op = handel(i);
            op ? this.options.push({ ...op, option: { value: e.im.option[i] } }) : '';
          });
        }
      } else {
        this.settingItem = null;
        this.type = 'form';
      }
    },
    blur() {
      const { settingItem, options } = this;
      options.map((im) => {
        const { option, key } = im;
        settingItem.im.option[key] = option.value;
      });
      this.$refs.left.changeOption(settingItem);
    },
    update(res) {
      this.settingItem.im.option = res;
      this.$refs.left.changeOption(this.settingItem);
    },
    open(flag) {
      if (this.type !== 'form') {
        this.drawer = flag;
        if (!flag) {
          this.update(this.$refs.chart.jsonUpdate());
        }
      }
    },
  },
  mounted() {},
  computed: {},
  watch: {},
};
</script>

<style lang='scss' scoped>
.rightTool {
  position: fixed;
  width: 200px;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: scroll;
  background-color: white;
  transition: all 0.5s ease;
}
.openRight {
  width: 40%;
  transition: all 0.5s ease;
}
</style>